
body {
  background-color: #fefefe;
  text-align: center;
}
button {
  margin: 0;
  padding: 10px 25px;
  border-radius: 0;
  appearance: none;
  border: 1px solid #333;
  background-color: #333;
  color: #fff;
  cursor: pointer;
}
input:not[type=checkbox] {
  padding: 10px 10px;
  border: 1px solid #ddd;
  appearance: none;
  margin-right: 3px;
}
table {
  max-width: 900px;
  margin-left: auto;
  margin-right: auto;
  border: 1px solid #ddd;
  border-collapse: collapse;
}
table td, table th {
  padding: 5px;
  border: 1px solid #efefef;
}
.completed {
  text-decoration: line-through;
  background-color: #f9f9f9;
}
